/** Basically ensure the URL isn't `javascript:alert('hi')` or something like that */
const isSafeUrl = text => {
  try {
    const url = new URL(text);
    return ['http:', 'https:'].includes(url.protocol);
  } catch (e) {
    return false;
  }
};

